import React, { useEffect, useState, useMemo, useRef } from 'react';
import { 
  Box,
  Typography,
  Divider
} from '@mui/material';
import { categoryList } from '../components/Calendar';

const moment = require('moment-timezone');

const Today = (props) => {
    const [currentDateTime, setCurrentDateTime] = useState();
    const [hours, setHours] = useState([]);
    const [currentTimePos, setCurrentTimePos] = useState(0);

    const currentTimeBlock = useRef(null);

    const eventsToday = useMemo(()=>{
      if(props.calendarData){
        let tempArray = props.calendarData;
        const resultArray = []

        //Sort by date/time
        tempArray.sort(function (a,b){
          return a.start.dateTime.localeCompare(b.start.dateTime)
        })

        tempArray = tempArray.filter(event=>{
          if(moment.utc(event.start.dateTime).tz('America/Chicago').format('YYYY-MM-DD') === currentDateTime){
            resultArray.push({
              ...event,
              start:{
                ...event.start,
                dateTime: moment.utc(event.start.dateTime).tz('America/Chicago').format('YYYY-MM-DD hh:mm:ss A')
              },
              end:{
                ...event.end,
                dateTime: moment.utc(event.end.dateTime).tz('America/Chicago').format('YYYY-MM-DD hh:mm:ss A')
              }
            })
          }
        })
        return resultArray
      }else{
        return []
      }
    },[props.calendarData])

    useEffect(() => {
      if (currentTimeBlock && currentTimeBlock.current) {
        currentTimeBlock.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }
    }, [currentTimePos]);

    useEffect(() => {
      const updateTimeline = () => {
        const currentTime = new Date();
        const totalSecondsInHour = (currentTime.getMinutes() * 60) + currentTime.getSeconds(); // Total seconds passed within the hour
        const totalSecondsInHourPercentage = (totalSecondsInHour / 3600) * 100; // Calculate percentage
        setCurrentTimePos(totalSecondsInHourPercentage);
      };
    
      const intervalId = setInterval(updateTimeline, 1000);
    
      return () => clearInterval(intervalId);
    }, []);

    useEffect(()=>{
      setCurrentDateTime(moment().tz('America/Chicago').format('YYYY-MM-DD'))
      let tempArray = []
      for(let i = 1; i<=23; i++){
        if(i>=12){
          if(i===12)
            tempArray.push({text:12 + " PM", time:i})
          else
            tempArray.push({text:i-12 + " PM", time:i})
        }else
          tempArray.push({text:i + " AM", time:i})
      }
      setHours(tempArray)
    },[])


    useEffect(()=>{
      console.log("Events today"+ eventsToday)
    },[eventsToday])

    return (
      <>
          <Box sx={{ pl: '35px', my: '10px' }}>
            {hours.map((hour) => {
              const eventsInHour = eventsToday.filter(
                (event) => moment(event.start.dateTime).hour() === hour.time
              );
              console.log(eventsInHour)
              const isCurrentHour = moment().hour() === hour.time;
    
              return (
                <Box key={hour.time} id={hour.time} ref={(isCurrentHour ? currentTimeBlock : null)} sx={{height: '14vh'}}>
                  {isCurrentHour && (
                    <div style={{ position: 'relative', right: '35px', top: `${currentTimePos}%`}}>
                      <div style={{ position: 'absolute', width: '100%', zIndex: 999, display: 'flex', alignItems: 'center' }}>
                        <Typography variant="body2" sx={{ marginRight: '5px', color: "red" }}>
                          {moment().format('hh:mm')}
                        </Typography>
                        <Divider sx={{ backgroundColor: 'red', height: '4px', width: '100%' }} />
                      </div>
                    </div>
                  )}
                  
                  <Divider sx={{backgroundColor: '#000', width: '100%' }} variant="inset" />
                  
                  <Box sx={{ display: 'flex'}} id={hour.time}>

                    <Typography>{hour.text}</Typography>
    
                    <Box sx={{ display: 'flex', ml: '10vh' }}>
                      {eventsInHour.map((event) => (
                        <Box sx={{backgroundColor:categoryList.find(color => event?.categories.includes(color[0])),p:2, height:"100%"}} key={event.id}>
                          <Typography sx={{color:categoryList.find(color => event?.categories.includes(color[0]))?"#FFF" : "#000"}}>{`${event?.subject} ${event.start.dateTime.split(" ")[1]} ${event.start.dateTime.split(" ")[2]}`} {" - "} {event.end.dateTime.split(" ")[1]}{event.end.dateTime.split(" ")[2]}</Typography>
                        </Box>
                      ))}
                    </Box>
                  </Box>
  
                </Box>
              );
            })}
          </Box>
      </>
    );
};

export default Today;