import React, {useState,useEffect} from "react";
import { Calendar as ReactCalendar, momentLocalizer, Views } from 'react-big-calendar'
import { 
    Box,
    Typography
} from "@mui/material";
import "../styles/react-big-calendar.css";
const moment = require('moment-timezone');

export const categoryList =
  [["Big Events", "yellow"], ["Brandon", "brown"], ["Call", "blue"], ["Dinner Meetings", "#b1514e"], ["done", "gray"], ["InService", "#d3642c"],
   ["Laura Saffle in Town", "#cc33a2"], ["Lunch meeting", "#c226d9"], ["norm", "#56a95f"], ["personal", "#d7bd28"], ["provider out", "#817c83"],
   ["stop in", "#35ca52"],["TO DO", "#33ccc7"], ["TRAVEL", "#a2875d"]]
  

const CustomEvent = ({ event }) => {
    let startTime = moment(event.start);
    startTime = startTime.format('hh:mm A');
    return(
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography sx={{fontSize:"12px", mr:"10px"}}>{startTime}</Typography>
            <Typography sx={{fontSize:"12px", textOverflow: "ellipsis", whiteSpace:"nowrap"}}>{event.title}</Typography>
        </Box>
    );
}

const customEventStyleGetter = (event) => {
  const eventColor = categoryList.find(color => event?.categories.includes(color[0]))
  return {
    style: {
      backgroundColor: eventColor? eventColor[1] : "lightblue",
      height: "20px"
    },
  };
};

export const Calendar = (props) => {

    const [events,setEvents] = useState([])
      
    const customComponents = {
        event: CustomEvent,
    };

    useEffect(()=>{
        let eventArray = []
        if(props.calendarData){
            props.calendarData.forEach(event =>{
                const startTime = moment.utc(event.start.dateTime);
                const endTime = moment.utc(event.end.dateTime);
                eventArray.push({
                    start: startTime.tz('America/Chicago').format('YYYY-MM-DD hh:mm A'), // 12-hour format
                    end: endTime.tz('America/Chicago').format('YYYY-MM-DD hh:mm A'), // 12-hour format
                    title: event?.subject,
                    categories: event?.categories
                })
            })
            console.log(eventArray)
            setEvents(eventArray)
        }
    },[])

    const localizer = momentLocalizer(moment)
    return (
        <div>
        <ReactCalendar
          {...props}
          eventPropGetter={customEventStyleGetter}
          showAllEvents={true}
          components={customComponents}
          localizer={localizer}
          events={events}
          startAccessor="start"
          endAccessor="end"
          views={['month']}
          style={{ height: '92vh' }}
          toolbar={false}
        />
      </div>
    );
};