import React, { useState, useEffect } from 'react';
import { useMsal } from '@azure/msal-react';
import { Calendar } from '../components/Calendar';
import { CircularProgress, Typography, Box } from '@mui/material';
import Today from './Today';

const moment = require('moment-timezone');

const Home = () => {
    
    const { instance, accounts } = useMsal();
    const [calendarData, setCalendarData] = useState(null);
    const [showCalendar, setShowCalendar] = useState(true);

    const getCalendarEvents = async () => {
        try {
          const accounts = instance.getAllAccounts(); // Get the signed-in accounts
          if (accounts.length > 0) {
            const account = accounts[0]; // Assuming you're using the first signed-in account
            const silentRequest = {
              scopes: ['https://graph.microsoft.com/.default'],
              account
            };
            const response = await instance.acquireTokenSilent(silentRequest);
            if (response.accessToken) {
              const accessToken = response.accessToken;
    
              // Use the access token to make a request to the Microsoft Graph API
              const graphEndpoint = 'https://graph.microsoft.com/v1.0/me/calendar/events';
              const graphResponse = await fetch(graphEndpoint, {
                headers: {
                  Authorization: `Bearer ${accessToken}`
                }
              });
    
              if (graphResponse.ok) {
                const data = await graphResponse.json();

                //sort data
                let tempArray = data.value
                tempArray.sort(function (a,b){
                  return a.start.dateTime.localeCompare(b.start.dateTime)
                })
                setCalendarData(tempArray)
              } else {
                console.error('Error fetching calendar events:', graphResponse.statusText);
              }
            } else {
              console.error('Access token not found.');
            }
          } else {
            console.error('No signed-in accounts found.');
          }
        } catch (error) {
          console.error('Error fetching calendar events:', error);
        }
      };

    useEffect(()=>{
      if(calendarData == null){
        getCalendarEvents()
      }
    },[calendarData])

    useEffect(() => {
        const intervalId = setInterval(() => {
          setShowCalendar((prevShowCalendar) => !prevShowCalendar); 
        }, 30000); 
    
        return () => clearInterval(intervalId);
      }, []);

    return (
        <Box sx={{maxWidth:"100%", overflowX: 'hidden'}}>
            {calendarData ? (
                <>
                    <Box sx={{my:"10px"}}>
                      <Typography sx={{ fontWeight: 800 }} variant='h4'>
                        {moment().format('MMMM Do h:mm A')}
                      </Typography>
                    </Box>
                   {/*  {showCalendar? <Calendar calendarData={calendarData}/> : <Today calendarData={calendarData}/>}  */}
                   <Today calendarData={calendarData}/>
                </>
            ) : (
                <CircularProgress/>
            )}
        </Box>
    );
};

export default Home;